import React, { useState } from "react"
import Title from "../components/title"
import Layout from "../components/layout"
import Seo from "../components/seo"
import "./styles/job-description.css"
import { Link, graphql, useStaticQuery } from "gatsby"
import { FaAngleDoubleRight } from "react-icons/fa"

const query = graphql`
  {
    allStrapiDigitopiaJob(sort: { fields: strapiId, order: ASC }) {
      nodes {
        strapiId
        strapiFields
        title
        position
        level
        desc {
          id
          name
        }
      }
    }
  }
`

const JobDescribtion = () => {
  const data = useStaticQuery(query);
  const [value,setValue] = useState(0);

  const {
    allStrapiDigitopiaJob: { nodes: jobs },
  } = data
  const { title, desc , level} = jobs[value]

  return (
    <>
    <Layout>
      <Seo title='Job Description' />
      <section className="section jobs">
        <Title title="Job Description" />
        <div className="jobs-center">
          <div className="btn-container">
            {jobs.map((item, index) => {
              return (
                <button
                  onClick={() =>setValue(index)}
                  className={`job-btn ${index === value && "active-btn"}`}
                  key={item.strapiId}
                >
                  {item.strapiFields}
                </button>
              )
            })}
          </div>

          <article className="job-info">
            <h3>{title}</h3><br/>
            <h4>{level} Level</h4>
            <h4>Full Time</h4>  
            <p className="job-date"></p>

            {desc.map(item => {
              return (
                <div key={item.id} className="job-desc">
                  <FaAngleDoubleRight className="job-icon"></FaAngleDoubleRight>
                  <p>{item.name}</p>
                </div>
              )
            })}
          </article>
        <Link to="/frontedApply" className="btn btn-center apply">
          Apply now
        </Link>
        </div>
      </section>
      </Layout>
    </>
  )
}
export default JobDescribtion
